@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,700);
/*
=============== 
Header
===============
*/

header {
  max-width: 1059px;
  margin: 3% auto 0 auto;
  text-align: center;
}

.scores {
  display: flex;
  justify-content: space-around;
}

.scores {
  display: flex;
}

/*
=============== 
Main Icon Grid
===============
*/

.basic-grid {
  display: grid;
  grid-gap: 2rem;
  gap: 2rem;
  max-width: 1100px;
  margin: 0 auto;
  padding: 3rem;
  grid-template-columns: repeat(5, minmax(150px, 1fr));
}

.card {
  display: flex;
  justify-content: center;
}

.icon {
  width: 150px;
  border-radius: 50%;
  cursor: pointer;
}

.icon:hover {
  border: 5px solid #752727;
}

@media screen and (max-width: 975px) {
  .icon {
    width: 120px;
  }
  .basic-grid {
    grid-gap: 1.5rem;
    gap: 1.5rem;
    grid-template-columns: repeat(5, minmax(120px, 1fr));
  }
}

@media screen and (max-width: 790px) {
  .icon {
    width: 100px;
  }
  .basic-grid {
    grid-gap: 1rem;
    gap: 1rem;
    grid-template-columns: repeat(5, minmax(100px, 1fr));
  }
}

@media screen and (max-width: 660px) {
  .icon {
    width: 80px;
  }
  .basic-grid {
    grid-template-columns: repeat(5, minmax(80px, 1fr));
  }
}

@media screen and (max-width: 560px) {
  .basic-grid {
    grid-template-columns: repeat(3, minmax(80px, 1fr));
  }
  .basic-grid {
    padding: 1rem;
  }
}

/*
=============== 
Global Styles
===============
*/

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #363a55;
  color: #fff;
  font-family: 'Poppins', sans-serif;
  max-width: 100vw;
}

h1 {
  font-size: 1.1rem;
}
h3 {
  font-size: 0.8rem;
}

p {
  font-size: 0.75rem;
}

@media screen and (min-width: 520px) {
  h1 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.25rem;
  }

  p {
    font-size: 1rem;
  }
}

@media screen and (min-width: 800px) {
  h1 {
    font-size: 3.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  p {
    font-size: 1.25rem;
  }
}

